<script>
import Navbar from "@/components/Navbar.vue";
import ToastList from "@/components/ToastList.vue";
import { ref, onMounted } from "vue";
import axios from "axios"; // 替代 this.$http
import Footer from "@/components/Footer.vue";
export default {
  components: {
    Navbar,
    ToastList,
    Footer,
  },
  data() {
    return {
      activeIndex: 0,
      isLoading: false,
      products: [],
      categories: [],
      selectListCategory: [],
    };
  },
  methods: {
    addCart(item) {
      const api = `${process.env.VUE_APP_API_PATH}/api/${process.env.VUE_APP_API_NAME}/cart`;
      this.$http.post(api, { data: { product_id: item.id, qty: 1 } }).then((res) => {
        console.log(res);
        if (res.data.success) {
          this.$root.emitter.emit("push-message", {
            style: "success",
            message: "已加入購物車",
            content: "",
          });
        }
      });
    },
    isActive(category) {
      return this.$route.path === category.path;
    },
    filterItemList(products, categoryName) {
      return products.filter((item) => {
        return item.category === categoryName;
      }).length;
    },
    changeCategory(index) {
      this.activeIndex = index;
      this.selectListCategory =
        this.activeIndex === 0
          ? this.products
          : this.products.filter(
              (item) => item.category === this.categories[index].split(" ")[0]
            );
    },
  },
  created() {
    const api = `${process.env.VUE_APP_API_PATH}/api/${process.env.VUE_APP_API_NAME}/products`;
    this.isLoading = true;
    this.$http.get(api).then((res) => {
      console.log(res);
      if (res.data.success) {
        const products = res.data.products;
        this.products = products;
        this.isLoading = false;

        this.categories = [
          `全部商品 All (${products.length})`,
          `飛行眼鏡 Aviator Sunglasses (${this.filterItemList(products, "飛行眼鏡")})`,
          `方形眼鏡 Square Sunglasses (${this.filterItemList(products, "方形眼鏡")})`,
          `圓形眼鏡 Round Sunglasses (${this.filterItemList(products, "圓形眼鏡")})`,
          `偏光眼鏡 Polarized Sunglasses (${this.filterItemList(products, "偏光眼鏡")})`,
          `造型眼鏡 Styling Sunglasses (${this.filterItemList(products, "造型眼鏡")})`,
        ];
        this.selectListCategory = this.products;
      }
    });
  },
};
</script>
